export default class StorageService {
  /**
   * La fonction permet de save une cl√© dans le local storage de l'application.
   */
  static setStorage(key, value) {
    try {
      localStorage.setItem(key, value);
    } catch (err) {
      console.error(err);
    }
  }

  /**
   * La fonction permet de r√©cup√©rer une cl√© dans le local storage de l'application.
   */
  static getStorage(key) {
    try {
      const res = localStorage.getItem(key);
      return res;
    } catch (err) {
      console.error(err);
    }
  }

  /**
   * La fonction permet de supprimer une (ou plusieurs) cl√©(s) dans le local storage de l'application.
   */
  static deleteStorageItem(...names) {
    try {
      names.forEach((name) => {
        localStorage.removeItem(name);
      });
    } catch (err) {
      console.error(err);
    }
  }
}
