import { useState } from 'react';

export const useForm = () => {
  const [state, setState] = useState({});

  const handleChange = (e) => {
    // Need to persist the event
    e.persist();
    if (e.target.type === 'file') {
      // Dans le cas o√π l'input est un input d'importation de fichiers et pas du simple texte, on g√®re un peu diff√©remment
      if (e.target.name === 'images') {
        // nom par d√©faut du component Upload. Accepte une ou plusieurs images, on les mets dans un tableau
        setState(() => ({
          ...state,
          [e.target.name]: Object.values(e.target.files),
        }));
      } else {
        // Ici c'est un champ sp√©cifique comme la preuve d'acte de d√©c√®s, pas besoin de le mettre dans un tableau car il n'y en aura jamais plusieurs
        setState(() => ({ ...state, [e.target.name]: e.target.files[0] }));
      }
    } else {
      setState(() => ({ ...state, [e.target.name]: e.target.value }));
    }
  };

  return [state, handleChange];
};

export const toFormData = (values) => {
  const data = new FormData();
  Object.keys(values).forEach((value) => {
    const keyName = value;
    const keyValue = values[value];
    if (keyName === 'images') {
      // Comme sp√©cifi√© dans la documentation de l'API, pour les input file g√©n√©rique 'image', on envoie tous les fichiers avec en cl√© le nom du fichier.
      keyValue.forEach((file) => {
        data.append(file.name, file);
      });
    } else {
      data.append(keyName, keyValue);
    }
  });
  console.log(...data);
  return data;
};
