import React, { useContext, useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import SessionService from '../../../services/SessionService';
import UserContext from '../../../context/UserContext';
import { useForm } from '../../../utils/forms';
import Loading from '../../utilsTemplates/loading/Loading';

function LoginForm({ location }) {
  const [values, handleChange] = useForm();
  const { user, setUser } = useContext(UserContext);
  const [isLoading, setIsLoading] = useState(false);

  if (isLoading) return <Loading />;

  const redirectAfterLogin = location.state
    ? location.state.from.pathname
    : '/'; // si l'utilisateur a √©t√© redirig√© vers login en essaynt d'acc√©der √† une RestrictedRoute, on la garde en m√©moire pour le rediriger apr√®s le login. Sinon, on le redirgie vers la home apr√®s le login.

  if (user) return <Redirect to={redirectAfterLogin} />; // Si l'utilisateur est connect√©, il ne peut pas voir la route /login (sans se d√©connecter), on le redirige donc vers la page depuis laquelle il a essay√© d'acc√©der √† Login

  async function persistSession() {
    await SessionService.requestTokens(values.email, values.password);
    setUser(await SessionService.fetchUserData());
    setIsLoading(false); // fin du loading
  }

  const handleLogin = (event) => {
    event.preventDefault(); // Emp√™cher le refresh de la page lors de l'envoi du formulaire
    setIsLoading(true); // d√©but du loading
    persistSession();
  };

  return (
    <div>
      <form onSubmit={handleLogin}>
        <label htmlFor="email">
          Adresse e-mail
          <input
            type="email"
            name="email"
            id="email"
            placeholder="test@example.com"
            value={values.email || ''}
            onChange={handleChange}
          />
        </label>
        <label htmlFor="password">
          Mot de passe
          <input
            type="password"
            name="password"
            id="password"
            value={values.password || ''}
            onChange={handleChange}
          />
        </label>
        <input type="submit" value="Me connecter" />
        <Link to="/">Home</Link>
        <Link to="/register">S'inscrire</Link>
      </form>
    </div>
  );
}

export default LoginForm;
